@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;300;400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background:#0d1117;
  color: lightgrey;
  font-family: "Poppins";
}

.App {
  text-align: -webkit-center;
}

.App h1 {
  margin: 2rem 0 2rem 0;
  color: lightgrey;;
}

.App h1:after {
  position: absolute;
  width: 8rem;
  height: 1.5px;
  bottom: 36.7rem;
  left: 45%;
  background-color: #ed145b;
  content: "";
}

a {
  text-decoration: none;
}

h1, h2 {
  font-weight: 300;
}

@media (max-width: 768px) {
  .App h1:after {
   display: none;
  }  
}

.movies-list {
    align-items: end;
    display: grid;
    grid-template-columns: auto auto auto auto auto;
    list-style: none;
    padding: 0;
}

.movies-list li {
    width: -webkit-min-content;
    width: -moz-min-content;
    width: min-content;
    margin: 0;
}

.movies-list p {
    max-width: 11.6rem;
    margin: 1.7rem 0 0.4rem 0.8rem;
    text-align: center;
    color: lightgrey;
}

.movies-list img {
    width: 12.5rem;
    border-radius: 8px;
    box-shadow: 3px 3px 12px #000000;
}

.movies-list img:hover {
    cursor: pointer;
    opacity: 0.3;
    transition: 1s;
}

@media (max-width: 768px) {
    .movies-list {
        display: block;
        grid-template-columns: auto auto auto;
    }
  }
.movie-details {
  width: 80vw;
  display: flex;
}

.movie-details img:nth-child(2) {
  height: 45vh;
  border-radius: 8px;
  margin: 0 0.2rem 0 0.7rem;
  box-shadow: 3px 3px 12px #000000;
}

.movie-img-title {
  border-right: 1px solid #6a6c6f;
  padding-right: 2.3rem;
}

.movie-img-title h2 {
  border: 1.3px solid #ed145b;
  text-align: center;
  margin-left: 12px;
  border-radius: 5px;
}

.movie-img-title span {
  margin: 0;
  color: #ed145b;
}
.movie-img-title img:first-child {
  width: 1.7rem;
  float: left;
  margin: 0 0 0.2rem 2rem;
}

.movie-description h3 {
  margin: 0;
}

.movie-description h3:nth-child(2) {
  color: #ed145b;
}

.movie-description article {
  margin: 1rem 4rem 0 4rem;
  text-align: left;
}

@media (max-width: 768px) {
  .movie-details {
      display: block;
  }
  .movie-img-title {
    border-right: none;
    margin-bottom: 2rem;
    padding-right: 0.7rem;
  }
  .movie-img-title img:first-child {
    margin: 0 0 0.2rem 3rem;
  }

  .movie-description article {
    margin: 0.3rem 0.1rem ;
    text-align: left;
  }
}
